import axios from '@/utils/uniAxios.js'
export default {
	data() {
		return {
			page:{
				current:1,
				pageSize:10
			},
			list:{
				yyAmount:'',//账户余额
				notRecordedAmount:'',//未入账金额
			}
		}
	},
	onLoad() {
		this.getData()
	},
	methods:{
		getData(){
			let obj = {
				pageNum:this.page.current,
				pageSize:this.page.pageSize
			}
			axios.post('/income/list',obj).then(res=>{
				console.log(res)
				this.list = res.data.data
			})
		},
		next(){
			uni.navigateTo({
				url:'billstatistics'
			})
		}
	}
}
